import { useEffect, useState } from "react";
import "./styles.css";

export default function App() {
  const [state, setState] = useState([]);
  const [display, setDisplay] = useState(0);

  useEffect(() => {
    fetch("https://api.punkapi.com/v2/beers")
      .then((response) => response.json())
      .then((data) => setState(data));
  }, []);

  return (
    <div className="App">
      <h1>Hello World! :D</h1>

      <select onChange={(e) => setDisplay(e.target.value)}>
        {state.map((val, idx) => (
          <option key={val.id} value={idx}>
            {val.name}
          </option>
        ))}
      </select>
      <Card displayData={display} displayState={state} />
    </div>
  );
}

export function Card({ displayData, displayState }) {
  console.log(displayData, displayState);
  return (
    <div className="Card">
      <p>{displayState[displayData]?.tagline}</p>
      <img
        src={displayState[displayData]?.image_url}
        alt={displayState[displayData]?.name}
      />
    </div>
  );
}
